@import "../fonts/Fonts";

.menu{
  padding: 0 25px;
  font-size: .9em;
  .menu_item.active{
    color: hsla(354, 57%, 61%, 1);
  }
  a{
    text-decoration: none;
    color: inherit;
  }
  .menu_item{
    display: flex;
    margin-top: 5px;
    .menu_icon,.menu_label{
      height: 20px;
      display: flex;
      align-items: center;
    }
    .menu_icon{
      margin-right: 5px;
      padding-top: 3px;
    }
  }

}