@charset "UTF-8";

@font-face {
  font-family: "untitled-font-1";
  src:url("./resources/untitled-font-1.eot");
  src:url("./resources/untitled-font-1.eot?#iefix") format("embedded-opentype"),
  url("./resources/untitled-font-1.woff") format("woff"),
  url("./resources/untitled-font-1.ttf") format("truetype"),
  url("./resources/untitled-font-1.svg#untitled-font-1") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "untitled-font-1" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "untitled-font-1" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-folder:before {
  content: "\62";
}
.icon-sliders:before {
  content: "\65";
}
.icon-files:before {
  content: "\66";
}
.icon-dashboard:before {
  content: "\61";
}
.icon-search:before {
  content: "\63";
}
