@import "../fonts/Fonts";


.header{
  margin-bottom: 15px;
  .search{
    display: flex;
    background-color: white;
    border-radius: 8px;
    display: flex;
    width: 25vw;
    border: 1px solid #dfe1e5;
    .search_icon{
      color: hsla(354, 2%, 52%, 1);
      padding: 5px;
      padding-left: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .search_text{
      border: none;
      outline: none;
      padding: 5px;
      background: none;
      flex: 1;
      width: 100%;
      padding-right: 20px;
    }
  }
}