
.serviceproviders{

  .btn{
    display:flex;
    background-color: white;
    width: fit-content;
    padding: 5px 10px;
    border-radius: 8px;
    border: 1px solid #dfe1e5;
    font-size: .9em;
    cursor: pointer;
    .icon{
      margin-right: 5px;
    }
  }

  &>.title{
      color: hsla(354, 57%, 61%, 1);
  }

  .serviceprovider--list{
    margin-top: 10px;
    background-color: white;
    border: 1px solid #dfe1e5;
    border-radius: 8px;
    &>.ctrls{
      display: flex;
      padding: 10px 25px;
      .new.btn{
        margin-left: auto;
      }
      a{
        text-decoration: none;
        color: inherit;
      }
    }
  }

  .serviceprovider--new{
    margin-top: 10px;
    background-color: white;
    border: 1px solid #dfe1e5;
    border-radius: 8px;
    font-size: .9em;

    &>.title{
      padding: 10px 25px;
      font-weight: bolder;
    }
    &>.serviceprovider--form--body{
      padding: 10px 25px;
      border-top: 1px solid #dfe1e5;
      border-bottom: 1px solid #dfe1e5;
      background-color: hsla(354, 47%, 97%, 1);
      display: flex;
      flex-direction: column;
      align-items: center;

      .field{
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
        width: 25vw;

        .field_label{
          margin-bottom: 5px;
        }

        input[type=text],
        input[type=tel],
        input[type=password],
        select{
          padding: 8px 10px;
          border-radius: 8px;
          border: 1px solid #dfe1e5;
          outline: none;
          font-size: .9em;
        }
      }

    }
    &>.serviceprovider--form--footer{
      display: flex;
      padding: 10px 25px;
      .cancel.btn{
        margin-left: auto;
      }
      .create.btn{
        background-color: #2f9cf0;
        color: #e5fbfe;
        margin-left: 15px;
      }
      a{
        text-decoration: none;
        color: inherit;
      }
    }
  }

}